  addEvent(event: MatDatepickerInputEvent<Date>) {
    this.day = event.value;
  }

  select(): void {
    const store = this.store;
    console.log(store);
    this.predayFormat = parseInt(formatDate(this.day, 'MMdd', 'en-US')) - 1;
    if (this.predayFormat < 1000) {
      this.predayFormatStr = '0' + this.predayFormat.toString();
    } else {
      this.predayFormatStr = this.predayFormat.toString();
    }
    console.log(this.predayFormatStr);

    if (store) {
      this.stocks$ = this.db
        .collection(store)
        .doc(this.predayFormatStr)
        .collection(this.predayFormatStr)
        .valueChanges();
    }
    this.list = [];
  }

