  findEndBal(item: string): string{
    for (let i = 0; i < this.list.length; i++) {
      if(item===this.list[i].getItem()){
        return this.list[i].getEndbalance();
      }
    }
    return '0';
  }


  calDiffBSCwithMYOB(
    item: string,
    begBalance: string,
    inputRef: HTMLInputElement
  ): void {
    const numMYOB = parseInt(inputRef.value.trim());
    const numDB = parseInt(begBalance);
    console.log('MYOB ' + numMYOB);
    console.log('FB db' + numDB);

    if (numMYOB) {
      console.log('In la');
      console.log('inputRef.value ' + inputRef.value);
      const diffBegSCwithMYOB = (numMYOB - numDB).toString();
      console.log('diffBegSCwithMYOB ' + diffBegSCwithMYOB);
      let count = 0;
      // tslint:disable-next-line: prefer-for-of
      for (let i = 0; i < this.list.length; i++) {
        console.log('getItem ' + this.list[i].getItem());
        console.log('item ' + item);
        if (this.list[i].getItem() == item) {
          this.list[i].setDiff(diffBegSCwithMYOB);
          break;
        }
        count++;
      }
      console.log(count);
      if (count === this.list.length) {
        console.log('create a new myob');
        this.list.push(
          new Myob(
            item,
            numMYOB.toString(),
            numDB.toString(),
            diffBegSCwithMYOB
          )
        );
      }
    }
  }

