import { Data } from '@angular/router';

export class Myob {
  private item = '';
  private myob = '';
  private sc = '0';
  private diff = '';
  private stockin = '0';
  private stockout = '0';
  private sold = '0';
  private endbalance = '';
  private endmyob = '';
  private enddiffbamy = '';
  private endstockcount = '';
  private enddiffmysc = '';
  private preorder = '';
  private preorderinfo = '';
  private upload;

  constructor(item: string, myob: string, sc: string, diff: string) {
    this.item = item || '';
    this.myob = myob || '';
    this.sc = sc || '';
    this.diff = diff || '';
  }

  getItem(): string {
    return this.item;
  }
  getMyob(): string {
    return this.myob;
  }
  getSc(): string {
    return this.sc;
  }
  getDiff(): string {
    return this.diff;
  }
  getStockin(): string {
    return this.stockin;
  }
  getStockout(): string {
    return this.stockout;
  }
  getSold(): string {
    return this.sold;
  }
  getEndbalance(): string {
    return this.endbalance;
  }
  getEndmyob(): string {
    return this.endmyob;
  }
  getEnddiffbamy(): string {
    return this.enddiffbamy;
  }
  getEndstockcount(): string {
    return this.endstockcount;
  }
  getEnddiffmysc(): string {
    return this.enddiffmysc;
  }
  getPreorder(): string {
    return this.preorder;
  }
  getPreorderinfo(): string {
    return this.preorderinfo;
  }
  getUpload() {
    this.upload = {
      item: this.endbalance
    }
    return this.upload;
  }

  setItem(item: string): void {
    this.item = item;
  }
  setMyOB(myob: string): void {
    this.myob = myob;
  }
  setSc(sc: string): void {
    this.sc = sc;
  }
  setDiff(diff: string): void {
    this.diff = diff;
  }
  setStockin(stockin: string): void {
    this.stockin = stockin;
  }
  setStockout(stockout: string): void {
    this.stockout = stockout;
  }
  setSold(sold: string): void {
    this.sold = sold;
  }
  setEndbalance(): void {
    this.endbalance = (
      parseInt(this.sc) +
      parseInt(this.stockin) -
      parseInt(this.stockout) -
      parseInt(this.sold)
    ).toString();
  }
  setEndmyob(endmyob: string): void {
    this.endmyob = endmyob;
  }
  setEnddiffbamy(): void {
    this.enddiffbamy = (
      parseInt(this.endbalance) - parseInt(this.endmyob)
    ).toString();
  }
  setEndstockcount(endstockcount: string): void {
    this.endstockcount = endstockcount;
  }
  setEnddiffmysc(): void {
    this.enddiffmysc = (
      parseInt(this.endstockcount) - parseInt(this.endmyob)
    ).toString();
  }
  setPreorder(preorder: string): void {
    this.preorder = preorder;
  }
  setPreorderinfo(preorderinfo: string): void {
    this.preorderinfo = preorderinfo;
  }

}
