import { Component, OnInit } from '@angular/core';
import { FormGroup, FormBuilder, Validators } from '@angular/forms';

import { AngularFireAuth } from 'angularfire2/auth';
import * as firebase from 'firebase/app';

import { Router } from '@angular/router';
import { appPath } from '../appPath';

@Component({
  selector: 'app-login',
  templateUrl: './login.component.html',
  styleUrls: ['./login.component.css'],
})
export class LoginComponent implements OnInit {

  signinform: FormGroup;
  constructor(
    public router: Router,
    public fb: FormBuilder,
    public afAuth: AngularFireAuth
  ) {
    this.signinform = fb.group({
      email: [
        null,
        Validators.compose([Validators.email, Validators.required]),
      ],
      password: [
        null,
        Validators.compose([
          // Validators.pattern('^(?=.*[a-zA-Z])([a-zA-Z0-9]+)$'),
          Validators.minLength(6),
          Validators.maxLength(25),
          Validators.required,
        ]),
      ],
    });

    console.log(this.signinform);
    this.signinform.valueChanges.subscribe((form: any) => {
      console.log('form changed to:', form);
    });
  }

  trySignIn() {
    console.log('GG');
    console.log('Reactive Form Data:', this.signinform.get('email').value);
    console.log('Reactive Form Data:', this.signinform.get('password').value);

    this.afAuth.auth
      .signInWithEmailAndPassword(
        this.signinform.get('email').value,
        this.signinform.get('password').value
      )
      .then(() => {
        const user = firebase.auth().currentUser;
        if (user) {
          console.log('we did it');
          console.log(user);
          this.router.navigate([appPath.empty], {
            queryParams: {
              status: '10011111001011', //ok in bin
            },
          });
        }
      })
      .catch((error) => {
        console.log(error.message);
      });
  }

  checkstatus() {
    console.log('it works -checkstatus');
    if (firebase.auth().currentUser != null) {
      console.log('we have a user');
      const user = firebase.auth().currentUser;
      console.log(user.email);
    } else {
      console.log('NO USER~~~~~~~~~~~~~~~');
    }
  }

  signout() {
    console.log('it works - signout');
    firebase.auth().signOut();
    const user = firebase.auth().currentUser;
    if (user != null) {
      console.log('we did not sign out');
      console.log(user);
    } else {
      console.log('YES, WE SINGOUT~~~~~~~~~~~~~~~');
    }
  }

  ngOnInit(): void {}
}
